.TodoListItem {
  background-color: #444752;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoListItem-p {
  margin: 24px px 24px 24px;
  width: calc(100% - 120px);
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: aliceblue;
}

.TodoListItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  color: aliceblue;
  margin: 5px;

}

.Icon-check {
  position: absolute;
  left: 12px;
}

.Icon-check:hover {
  color: #4caf50;
}

.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 0;
}

.Icon-delete:hover {
  color: red;
}

.Icon-edit {
  position: absolute;
  right: 50px;
}

.Icon-edit:hover {
  color: rgb(0, 166, 255);
}